// src/ProdutoManager.js
import React, { useState, useEffect } from 'react';
import { Table, Input, Button, Form, InputNumber, Layout } from 'antd';
import api from './api'; // Importa a configurao da API
import 'antd/dist/reset.css';

const { Header, Content } = Layout;

function ProdutoManager() {
  const [form] = Form.useForm();
  const [produtos, setProdutos] = useState([]);

  // Funo para carregar os produtos ao montar o componente
  useEffect(() => {
    fetchProdutos();
  }, []);

  // Funo para buscar produtos da API
  const fetchProdutos = async () => {
    try {
      const response = await api.get('/produtos'); // Requisio GET para buscar produtos
      setProdutos(response.data); // Armazena os produtos no estado
    } catch (error) {
      console.error('Erro ao buscar produtos:', error);
    }
  };

  // Funo para adicionar um novo produto
  const onFinish = async (values) => {
    try {
      const response = await api.post('/produtos', values); // Envia o novo produto para a API
      setProdutos([...produtos, response.data]); // Adiciona o novo produto  lista exibida
      form.resetFields(); // Limpa o formulrio aps adicionar
    } catch (error) {
      console.error('Erro ao adicionar produto:', error);
    }
  };

  // Funo para remover um produto pelo ID
const handleRemove = async (id) => {
    try {
      await api.delete(`/produtos/${id}`); // Corrigido: uso correto de template string
      setProdutos(produtos.filter((produto) => produto.id !== id)); // Remove o produto da lista exibida
    } catch (error) {
      console.error('Erro ao remover produto:', error);
    }
  };
  
  const columns = [
    {
      title: 'ID',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: 'Nome do Produto',
      dataIndex: 'nome',
      key: 'nome',
    },
    {
      title: 'Preo de Custo',
      dataIndex: 'precoCusto',
      key: 'precoCusto',
    },
    {
      title: 'Preo de Venda',
      dataIndex: 'precoVenda',
      key: 'precoVenda',
    },
    {
      title: 'Quantidade',
      dataIndex: 'quantidade',
      key: 'quantidade',
    },
    {
      title: 'Ao',
      key: 'acao',
      render: (_, record) => (
        <Button type="link" onClick={() => handleRemove(record.id)} danger>
          Remover
        </Button>
      ),
    },
  ];

  return (
    <Layout style={{ minHeight: '100vh', padding: '24px', background: '#f5f5f5' }}>
      <Header style={{ backgroundColor: '#1B93AF', textAlign: 'center', display: 'flex',alignItems: 'center',justifyContent:'center',padding: '20px' }}>
        <h1 style={{ color: 'black',margin: 0}}>Tabela de Produtos</h1>
      </Header>
      <Content style={{ marginTop: '24px' }}>
        <Form
          form={form}
          layout="inline"
          onFinish={onFinish}
          style={{ display: 'flex', justifyContent: 'center', gap: '10px', marginBottom: '20px' }}
        >
          <Form.Item
            name="nome"
            rules={[{ required: true, message: 'Por favor, insira o nome do produto!' }]}
          >
            <Input placeholder="Nome do Produto" />
          </Form.Item>
          <Form.Item
            name="precoCusto"
            rules={[{ required: true, message: 'Por favor, insira o preo de custo!' }]}
          >
            <InputNumber placeholder="Preo de Custo" min={0} style={{ width: '100%' }} />
          </Form.Item>
          <Form.Item
            name="precoVenda"
            rules={[{ required: true, message: 'Por favor, insira o preo de venda!' }]}
          >
            <InputNumber placeholder="Preo de Venda" min={0} style={{ width: '100%' }} />
          </Form.Item>
          <Form.Item
            name="quantidade"
            rules={[{ required: true, message: 'Por favor, insira a quantidade!' }]}
          >
            <InputNumber placeholder="Quantidade" min={0} style={{ width: '100%' }} />
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit">
              Adicionar
            </Button>
          </Form.Item>
        </Form>

        <Table
          dataSource={produtos}
          columns={columns}
          rowKey="id"
          pagination={false}
          style={{ margin: '0 auto', maxWidth: '80%', backgroundColor: 'white' }}
        />
      </Content>
    </Layout>
  );
}

export default ProdutoManager;